.dashboard .Dashboard-header {
  background-color: #2550a5;
  height: 550px;
}
.tabs {
  display: flex;
  flex-direction: column;
  height: inherit;
}
.tabs .tabs-header {
  display: flex;
  background-color: #cfdfbd;
}
.tabs .tabs-header .tabs-header-tab {
  padding: 10px;
  cursor: pointer;
  font-size: 16px;
  color: #707070;
  transition: all 0.3s;
  font-family: "Courier New", monospace;
  text-align: center;
  width: 150px;
  font-weight: bold;
}
.tabs .tabs-header .tabs-header-tab.selected {
  font-weight: bold;
  color: #303030;
  background-color: #F5EAA6;
  border: none;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
}
.tabs .tabs-header .tabs-header-tab.inactive {
  background-color: #BCCBA5;
}
.tabs .tabs-header .tabs-header-tab:hover {
  background-color: #E1CEA6;
}
.tabs .tabs-content {
  flex: 1;
  padding: 20px;
  overflow: auto;
  background-color: #F5EAA6;
  font-weight: bolder;
}
